@font-face {
  font-family: "Lato";
  src: local("Lato"), url("../../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../../fonts/lato/Lato-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: local("Oswald"), url("../../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../../fonts/oswald/oswaldmedium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: local("Oswald"), url("../../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../../fonts/oswald/oswaldregular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100vh;
}

body {
  min-height: 100vh;
  margin: 0;
  font-family: "Lato", "Arial", sans-serif;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.middle-container {
  margin: 0 auto;
  width: 100%;
  max-width: 320px;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .middle-container {
    max-width: 1220px;
    padding: 0 30px;
  }
}
@media (min-width: 1220px) {
  .middle-container {
    padding: 0;
  }
}

.container {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  position: relative;
  overflow: hidden;
}

.button {
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #ffffff;
  font-weight: 400;
  font-family: "Oswald", "Arial", sans-serif;
  background-color: #68b738;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  padding: 8px 44px;
  border: none;
}
.button:active {
  background-color: #5eaa2f;
}
@media (min-width: 1220px) {
  .button {
    cursor: pointer;
  }
  .button:hover {
    background-color: #5eaa2f;
  }
}

.hero__content {
  padding: 0;
  display: grid;
}
@media (min-width: 1220px) {
  .hero__content {
    position: relative;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    margin: 0 auto 134px auto;
  }
}

.hero__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
