  font-size: 36px;
  line-height: 36px;
  color: #000000;
  font-weight: 400;
  font-family: "Oswald", "Arial", sans-serif;
  margin: 0 0 38px 0;
}
@media (min-width: 768px) {
  .program-form__title {
    font-size: 60px;
    line-height: 60px;
    margin: 0 0 54px 0;
  }
}
@media (min-width: 1220px) {
  .program-form__title {
    margin: 0 0 58px 0;
  }
}

.program-form__subtitle {
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
  font-weight: 400;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  margin: 0 0 38px 0;
  grid-column: 1;
}
@media (min-width: 768px) {
  .program-form__subtitle {
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 65px;
    margin-left: 2px;
  }
}
@media (min-width: 1220px) {
  .program-form__subtitle {
    margin: 0 0 63px 0;
  }
}

@media (min-width: 768px) {
  .program-form__container {
    display: flex;
    flex-direction: column;
    padding: 0 68px;
  }
}
@media (min-width: 1220px) {
  .program-form__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: 0 auto 13px auto;
    padding: 0;
    column-gap: 80px;
  }
}

.program-form__text {
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  color: #000000;
  font-weight: 400;
  margin: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .program-form__text {
    font-size: 16px;
    line-height: 16px;
    padding-bottom: 6px;
  }
}

.program-form__button {
  font-family: "Lato", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  font-weight: 400;
  font-family: "Oswald", "Arial", sans-serif;
  display: block;
  padding: 10px 44px;
  margin: 0 0 22px 0;
  width: 100%;
}
.program-form__button:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .program-form__button {
    font-size: 20px;
    padding: 16px 20px;
    margin: 0 0 0 2px;
    max-width: 282px;
    flex: 1;
  }
}
@media (min-width: 1220px) {
  .program-form__button {
    max-width: 568px;
    padding: 16px 20px;
  }
}

@media (min-width: 768px) {
  .program-form__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 109px 0 0;
  }
}
@media (min-width: 1220px) {
  .program-form__wrapper {
    grid-column: 1/-1;
    margin: 0;
    padding: 0;
    gap: 81px;
    justify-content: start;
  }
}

@media (min-width: 768px) {
  .header {
    display: flex;
    justify-content: space-between;
    padding: 0 30px;
    margin-bottom: 62px;
    max-width: 1220px;
    width: 100%;
    border: 0;
  }
}
@media (min-width: 1220px) {
  .header {
    justify-content: space-between;
    padding: 53px 0 0 0;
  }
}
@media (min-width: 1220px) {
  .header--catalog {
    margin: 0 auto 79px auto;
  }
}
.header--form {
  border-bottom: 1px solid #d9d9d9;
  margin: 0 0 27px 0;
}
@media (min-width: 768px) {
  .header--form {
    border: none;
    margin: 0 auto 65px auto;
  }
}
@media (min-width: 1220px) {
  .header--form {
    margin: 0 auto 78px auto;
  }
}
@media (min-width: 1220px) {
  .header--index {
    max-width: 100%;
    justify-content: space-between;
    padding: 53px 0;
    margin: 0 0 62px 0;
  }
  .header--index::before {
    content: "";
    position: absolute;
    background: linear-gradient(to left, #61a53a 0, #61a53a 50%, #ffffff 50%);
    width: 100%;
    height: 695px;
    z-index: -1;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

.header__container {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
@media (min-width: 1220px) {
  .header__container--index::after {
    content: "";
    background-image: url("../../img/cat-bg-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-position: 10px;
    background-size: 100%;
    position: absolute;
    width: 1040px;
    height: 697px;
    right: calc(50% - 1033px);
    top: 0;
    z-index: 0;
  }
}
@media (min-width: 1220px) and (min-resolution: 2dppx) {
  .header__container--index::after {
    background-image: url("../../img/cat-bg-desktop@2x.jpg");
  }
}
@media (min-width: 768px) {
  .header__container {
    flex-direction: row;
    padding: 0;
  }
}
@media (min-width: 1220px) {
  .header__container {
    margin: 0 auto;
    z-index: 2;
    padding: 0;
  }
}
@media (min-width: 1220px) {
  .header__container {
    margin: 0 auto;
    z-index: 2;
  }
}

@media (min-width: 768px) {
  .header__logo {
    padding-top: 7px;
  }
}
@media (min-width: 1220px) {
  .header__logo {
    padding-top: 22px;
  }
}

.header__logo-image {
  margin: 0 57px 0 0;
}

.header__wrapper {
  display: flex;
  align-items: center;
  padding: 9px 0;
  margin: 0;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .header__wrapper {
    height: 52px;
    margin: 0;
    padding: 0;
  }
  .header__wrapper::after {
    display: none;
  }
}
@media (min-width: 1220px) {
  .header__wrapper {
    width: 205px;
  }
}

.header__menu-button {
  width: 60px;
  height: 46px;
  position: relative;
  top: 0;
  right: 0;
  cursor: pointer;
  overflow: hidden;
  border: 0;
  background-color: inherit;
}
@media (min-width: 768px) {
  .header__menu-button {
    display: none;
  }
}

.header__button {
  width: 24px;
  height: 2px;
  position: absolute;
  background-color: #000000;
  top: 50%;
  left: 60%;
  transition: 0.5s;
  overflow: hidden;
}
.header__button:first-of-type {
  transform: translateX(0);
}
.header__button:nth-of-type(2) {
  top: calc(50% - 8px);
  transform: rotate(0);
}
.header__button:nth-of-type(3) {
  top: calc(50% + 7px);
  transform: rotate(0);
}

.header__menu-button--active .header__button:first-of-type {
  transform: translateX(-260%);
}
.header__menu-button--active .header__button:nth-of-type(2) {
  top: 50%;
  transform: rotate(45deg);
}
.header__menu-button--active .header__button:nth-of-type(3) {
  top: 50%;
  transform: rotate(-45deg);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main {
  flex-grow: 1;
}